import "core-js/modules/es.array.push.js";
import "core-js/modules/es.iterator.constructor.js";
import "core-js/modules/es.iterator.filter.js";
import "core-js/modules/es.iterator.map.js";
import axios from 'axios';
export default {
  name: 'PublicBrowse',
  data() {
    return {
      files: [],
      allowedDirs: [],
      rootDir: '',
      // 根目录（从URL获取的第一级目录）
      currentPath: '',
      // 当前完整路径
      totalCount: 0,
      loading: false,
      error: null,
      canRetry: true,
      hasMore: true,
      previewVisible: false,
      previewIndex: 0
    };
  },
  computed: {
    // 根目录显示名称
    rootDirName() {
      return this.rootDir.split('/').filter(Boolean).pop() || '根目录';
    },
    // 当前路径相对于根目录的部分
    pathParts() {
      if (!this.currentPath || !this.rootDir) return [];
      const relative = this.currentPath.replace(this.rootDir, '').replace(/^\/+/, '');
      return relative.split('/').filter(Boolean);
    },
    // 媒体文件（用于预览）
    mediaFiles() {
      return this.files.filter(f => !f.isFolder && (this.isImage(f) || this.isVideo(f)));
    },
    // 当前预览的文件
    currentPreviewFile() {
      return this.mediaFiles[this.previewIndex];
    }
  },
  watch: {
    '$route.params.dir': {
      handler() {
        this.initFromRoute();
      }
    }
  },
  mounted() {
    this.initFromRoute();
  },
  methods: {
    async initFromRoute() {
      // 从URL获取目录路径: /browse/landscape/sub -> landscape/sub
      const dirParam = this.$route.params.dir || '';
      const dirPath = Array.isArray(dirParam) ? dirParam.join('/') : dirParam;
      if (!dirPath) {
        this.error = '请指定要浏览的目录，例如: /browse/landscape';
        this.canRetry = false;
        return;
      }

      // 提取根目录（第一级）
      const parts = dirPath.split('/').filter(Boolean);
      this.rootDir = parts[0];
      this.currentPath = dirPath;

      // 验证目录权限并加载
      await this.loadFiles();
    },
    async loadFiles() {
      this.loading = true;
      this.error = null;
      this.canRetry = true;
      try {
        const res = await axios.get(`/api/public/list?dir=${encodeURIComponent(this.currentPath)}&count=50`);

        // 保存允许的目录列表
        if (res.data.allowedDirs) {
          this.allowedDirs = res.data.allowedDirs;
        }
        const dirs = (res.data.directories || []).map(d => ({
          name: d,
          isFolder: true
        }));
        const files = (res.data.files || []).map(f => ({
          name: f.name,
          isFolder: false,
          metadata: f.metadata
        }));
        this.files = [...dirs, ...files];
        this.totalCount = res.data.totalCount || this.files.length;
        this.hasMore = this.files.filter(f => !f.isFolder).length < this.totalCount;
      } catch (err) {
        if (err.response?.status === 403) {
          const msg = err.response?.data?.error || '';
          if (msg.includes('disabled')) {
            this.error = '公开浏览功能未启用';
          } else if (msg.includes('not allowed') || msg.includes('No public')) {
            this.error = '该目录不允许公开访问';
          } else {
            this.error = '访问被拒绝';
          }
          this.canRetry = false;
        } else {
          this.error = '加载失败，请重试';
        }
      } finally {
        this.loading = false;
      }
    },
    async loadMore() {
      if (this.loading) return;
      this.loading = true;
      try {
        const start = this.files.filter(f => !f.isFolder).length;
        const res = await axios.get(`/api/public/list?dir=${encodeURIComponent(this.currentPath)}&start=${start}&count=50`);
        const moreFiles = (res.data.files || []).map(f => ({
          name: f.name,
          isFolder: false,
          metadata: f.metadata
        }));
        this.files.push(...moreFiles);
        this.hasMore = this.files.filter(f => !f.isFolder).length < this.totalCount;
      } catch (err) {
        alert('加载更多失败');
      } finally {
        this.loading = false;
      }
    },
    // 进入子文件夹
    enterFolder(folderPath) {
      const newPath = folderPath.replace(/\/+$/, '');
      this.$router.push(`/browse/${newPath}`);
    },
    // 返回根目录
    goToRoot() {
      this.$router.push(`/browse/${this.rootDir}`);
    },
    // 导航到指定层级
    goToPath(index) {
      const parts = this.pathParts.slice(0, index + 1);
      const newPath = this.rootDir + (parts.length ? '/' + parts.join('/') : '');
      this.$router.push(`/browse/${newPath}`);
    },
    getFolderName(path) {
      return path.split('/').filter(Boolean).pop() || path;
    },
    getFileName(path) {
      const name = path.split('/').pop();
      return name.length > 20 ? name.slice(0, 17) + '...' : name;
    },
    getFileUrl(name) {
      return `${window.location.origin}/file/${name}`;
    },
    isImage(file) {
      const ext = file.name.split('.').pop().toLowerCase();
      return ['jpg', 'jpeg', 'png', 'gif', 'webp', 'bmp', 'svg'].includes(ext);
    },
    isVideo(file) {
      const ext = file.name.split('.').pop().toLowerCase();
      return ['mp4', 'webm', 'ogg', 'mov'].includes(ext);
    },
    handleImageError(e) {
      e.target.style.display = 'none';
    },
    copyLink(name) {
      const url = this.getFileUrl(name);
      navigator.clipboard?.writeText(url).then(() => {
        alert('链接已复制');
      }).catch(() => {
        prompt('复制链接:', url);
      });
    },
    downloadFile(name) {
      const link = document.createElement('a');
      link.href = this.getFileUrl(name);
      link.download = name.split('/').pop();
      link.click();
    },
    openPreview(index) {
      const file = this.files[index];
      if (file.isFolder) return;
      // 找到在媒体文件中的索引
      const mediaIndex = this.mediaFiles.findIndex(f => f.name === file.name);
      if (mediaIndex >= 0) {
        this.previewIndex = mediaIndex;
        this.previewVisible = true;
        document.body.style.overflow = 'hidden';
      }
    },
    closePreview() {
      this.previewVisible = false;
      document.body.style.overflow = '';
    },
    prevImage() {
      if (this.previewIndex > 0) this.previewIndex--;
    },
    nextImage() {
      if (this.previewIndex < this.mediaFiles.length - 1) this.previewIndex++;
    }
  }
};